@import url(https://fonts.googleapis.com/css?family=Montserrat|Baskervville|Ibarra+Real+Nova&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css);
*{box-sizing:border-box}body{margin:0;font-family:'Montserrat', sans-serif;background:ghostwhite}h1,h2,h3{font-family:'Ibarra Real Nova', serif;font-family:'Baskervville', serif;font-weight:400}

.app .scroll{display:flex;align-items:center;justify-content:center;position:absolute;height:80px;width:110px;bottom:0;right:60px;background:#989898;border-radius:3px 3px 0 0;z-index:10}.app .scroll a,.app .scroll a:visited,.app .scroll a:active,.app .scroll a:focus{color:white;font-size:46px;-webkit-animation-name:bouncing;animation-name:bouncing;-webkit-animation-duration:1.2s;animation-duration:1.2s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-direction:normal;animation-direction:normal}@-webkit-keyframes bouncing{0%{margin-top:-3px}50%{margin-top:9px}100%{margin-top:-3px}}@keyframes bouncing{0%{margin-top:-3px}50%{margin-top:9px}100%{margin-top:-3px}}.app nav{border-radius:3px;position:absolute;top:calc(100% + 60px);width:110px;height:260px;right:60px;background:#d2d2d2;display:flex;justify-content:center}.app nav ul{width:100%;padding:0;margin:0;display:flex;flex-direction:column;align-items:center;justify-content:center}.app nav li{width:100%;display:flex;justify-content:center;align-items:center;list-style:none;flex-grow:1;-webkit-transition:all 0.3s;transition:all 0.3s;cursor:pointer}.app nav li a,.app nav li a:visited,.app nav li a:active,.app nav li a:focus{color:black;text-decoration:none}.app nav li:first-child{border-radius:3px 3px 0 0}.app nav li:last-child{border-radius:0 0 3px 3px}.app nav li.active{background:#bbb}.app nav li.active a{color:white}.app nav li:hover{background:#aaa}.app nav li:hover a{color:white}.app .hamburger{display:none;width:30px;cursor:pointer;flex-direction:column}.app .hamburger.open .line:nth-child(1){-webkit-transform:rotate(45deg) translateX(25%) translateY(50%);transform:rotate(45deg) translateX(25%) translateY(50%);margin-left:-12.5%}.app .hamburger.open .line:nth-child(2){background:none}.app .hamburger.open .line:nth-child(3){-webkit-transform:rotate(-45deg) translateX(25%) translateY(-50%);transform:rotate(-45deg) translateX(25%) translateY(-50%);margin-left:-12.5%}.app .hamburger .line{width:100%;height:3px;background:#232323;margin:3px;-webkit-transition:all 0.5s;transition:all 0.5s;border-radius:1.5px}@media only screen and (max-width: 1024px){.app .scroll{width:100% !important;left:0;bottom:0}.app nav{position:absolute;z-index:100;width:100%;right:0;height:auto;top:0 !important}.app nav ul{position:absolute;-webkit-transform:translateY(-100%);transform:translateY(-100%);background:#d2d2d2;width:100%;height:100vh;flex-direction:column;padding-top:60px;-webkit-transition:all 0.4s;transition:all 0.4s}.app nav ul li{opacity:0;-webkit-transform:translateY(-60%);transform:translateY(-60%)}.app nav ul.open{-webkit-transform:translateY(0%);transform:translateY(0%)}.app nav ul.open li{opacity:1;-webkit-transition:all 0.4s var(--delay);transition:all 0.4s var(--delay);-webkit-transform:translateY(0%);transform:translateY(0%)}.app .hamburger{display:block;position:fixed;top:20px;right:30px;z-index:100}}.app nav.sticky{position:fixed;top:60px}.app section{display:flex;min-height:100vh;height:auto;align-items:center;justify-content:center;flex-direction:column}.app section.content{justify-content:flex-start;padding:60px 0}.app section.content>div{display:flex;flex-direction:column;justify-content:center;align-items:center;width:60vw}.app section.content h2{font-size:28px;margin-top:0;font-weight:normal;padding-bottom:20px;border-bottom:1px solid #aaaaaa;text-align:center;width:80%}.app #hero{text-align:center;color:#dcdcdc}.app #hero .image{position:absolute;min-height:100%;width:100%;height:auto;top:0;left:0;background:url(/static/media/filmed-grey.35fb0fee.jpg) no-repeat center center;background-size:cover}.app #hero *:not(.image){z-index:10}.app #hero h1{margin-top:-100px;margin-bottom:20px;font-size:80px;padding-bottom:20px;border-bottom:1px solid #dcdcdc}.app #hero h2{font-size:28px;margin-top:0;font-weight:normal}.app #about{background:#989898;color:#fff}.app #work{color:#989898}.app #work .gallery{margin-top:40px;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:flex-end}.app #work .gallery .image{color:#fff;display:flex;align-items:center;justify-content:center;width:300px;height:200px;background:#989898;margin:10px}.app #contact{background:#989898;color:#fff}.app #contact .links{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}.app #contact a,.app #contact a:visited,.app #contact a:active,.app #contact a:focus{color:white;text-decoration:none;display:flex;align-items:center;padding:10px 20px;-webkit-transition:color 0.2s;transition:color 0.2s}.app #contact a:hover{color:#ddd}.app #contact i{font-size:36px;padding-right:10px}

