.app{

  .scroll{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 80px;
    width: 110px;
    bottom: 0;
    right: 60px;
    background: #989898;
    border-radius: 3px 3px 0 0;
    z-index: 10;

    a, a:visited, a:active, a:focus{
      color: white;
      font-size: 46px;

      animation-name: bouncing;
      animation-duration: 1.2s;
      animation-timing-function: ease-in;
      animation-iteration-count: infinite;
      animation-direction: normal;
    }

    @keyframes bouncing {
        0% { margin-top: -3px; }
      	50% { margin-top: 9px; }
      	100% { margin-top: -3px; }
    }
  }

  nav{
    border-radius: 3px;
    position: absolute;
    top: calc(100% + 60px);
    width: 110px;
    height: 260px;
    right: 60px;
    background: #d2d2d2;

    display: flex;
    justify-content: center;

    ul{
      width: 100%;
      padding: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    li{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      flex-grow: 1;
      transition: all 0.3s;
      cursor: pointer;

      a, a:visited, a:active, a:focus{
        color: black;
        text-decoration: none;
      }

      &:first-child{
        border-radius: 3px 3px 0 0;
      }
      &:last-child{
        border-radius: 0 0 3px 3px;
      }
      &.active{
        background: #bbb;
        a{
          color: white;
        }
      }
      &:hover{
        background: #aaa;
        a{
          color: white;
        }
      }
    }
  }

  .hamburger{
    display: none;
    width: 30px;
    // height: 60px;
    cursor: pointer;
    flex-direction: column;

    &.open{
      .line:nth-child(1){
        transform: rotate(45deg) translateX(25%) translateY(50%);
        margin-left: -12.5%;
      }
      .line:nth-child(2){
        background: none;
      }
      .line:nth-child(3){
        transform: rotate(-45deg) translateX(25%) translateY(-50%);
        margin-left: -12.5%;
      }
    }

    .line{
      width: 100%;
      height: 3px;
      background: #232323;
      margin: 3px;
      transition: all 0.5s;
      border-radius: 1.5px;
    }
  }

  @media only screen and(max-width: 1024px) {

    .scroll{
      width: 100% !important;
      left: 0;
      bottom: 0;
    }

    nav{
      position: absolute;
      z-index: 100;
      width: 100%;
      right: 0;
      height: auto;
      top: 0 !important;

      ul{
        position: absolute;
        transform: translateY(-100%);
        background: #d2d2d2;
        width: 100%;
        height: 100vh;
        flex-direction: column;
        padding-top: 60px;
        transition: all 0.4s;

        li{
          opacity: 0;
          transform: translateY(-60%);
        }

        &.open{
          transform: translateY(0%);
          li{
            opacity: 1;
            transition: all 0.4s var(--delay);
            transform: translateY(0%);
          }
        }
      }

    }

    .hamburger{
      display: block;
      position: fixed;
      top: 20px;
      right: 30px;
      z-index: 100;
    }

  }

  nav.sticky{
    position: fixed;
    top: 60px;
  }

  section{
    display: flex;
    min-height: 100vh;
    height: auto;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &.content{
      justify-content: flex-start;
      padding: 60px 0;

      > div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 60vw;
      }

      h2{
        font-size: 28px;
        margin-top: 0;
        font-weight: normal;
        padding-bottom: 20px;
        border-bottom: 1px solid #aaaaaa;
        text-align: center;
        width: 80%;
      }
    }
  }

  #hero{
    text-align: center;
    color: #dcdcdc;

    .image{
      position: absolute;
      min-height:100%;
      width: 100%;
      height: auto;
      top: 0;
      left: 0;
      background: url('./images/filmed-grey.jpg') no-repeat center center;
      background-size: cover;
    }

    *:not(.image) {
      z-index: 10;
    }

    h1{
      margin-top: -100px;
      margin-bottom: 20px;
      font-size: 80px;
      padding-bottom: 20px;
      border-bottom: 1px solid #dcdcdc;
    }

    h2{
      font-size: 28px;
      margin-top: 0;
      font-weight: normal;
    }
  }

  #about{
    background: #989898;
    color: #fff;
  }

  #work{
    color: #989898;
    .gallery{
      margin-top: 40px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: flex-end;

      .image{
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 300px;
        height: 200px;
        background: #989898;
        margin: 10px;
      }
    }
  }

  #contact{
    background: #989898;
    color: #fff;

    .links{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
    }


    a, a:visited, a:active, a:focus{
      color: white;
      text-decoration: none;
      display: flex;
      align-items: center;
      padding: 10px 20px;
      transition: color 0.2s;
    }

    a:hover{
      color: #ddd;
    }

    i{
      font-size: 36px;
      padding-right: 10px;
    }
  }

}
